off');\n  font-weight: 600;\n  font-style: normal;\n}\n@font-face {\n  font-family: 'Metropolis';\n  src: url('../fonts/metropolis-extrabold-webfont.eot?#iefix')\n      format('embedded-opentype'),\n    url('../fonts/metropolis-extrabold-webfont.woff2') format('woff2'),\n    url('../fonts/metropolis-extrabold-webfont.woff') format('woff');\n  font-weight: 700;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Metropolis';\n  src: url('../fonts/metropolis-thin-webfont.eot?#iefix')\n      format('embedded-opentype'),\n    url('../fonts/metropolis-thin-webfont.woff2') format('woff2'),\n    url('../fonts/metropolis-thin-webfont.woff') format('woff');\n  font-weight: 100;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Metropolis';\n  src: url('../fonts/metropolis-light-webfont.eot?#iefix')\n      format('embedded-opentype'),\n    url('../fonts/metropolis-light-webfont.woff2') format('woff2'),\n    url('../fonts/metropolis-light-webfont.woff') format('woff');\n  font-weight: 200;\n  font-style: normal;\n}\n","/*!\n * Hamburgers\n * @description Tasty CSS-animated hamburgers\n * @author Jonathan Suh @jonsuh\n * @site https://jonsuh.com/hamburgers\n * @link https://github.com/jonsuh/hamburgers\n */\n\n// Settings\n// ==================================================\n\n$hamburger-padding-x: 8px;\n$hamburger-padding-y: 8px;\n$hamburger-layer-width: 26px;\n$hamburger-layer-height: 3px;\n$hamburger-layer-spacing: 6px;\n$hamburger-layer-color: $light-color;\n$hamburger-layer-border-radius: 4px;\n$hamburger-hover-opacity: 0.7;\n$hamburger-active-layer-color: white;\n$hamburger-active-hover-opacity: $hamburger-hover-opacity;\n\n$hamburger-hover-use-filter: false;\n$hamburger-hover-filter: opacity(50%);\n$hamburger-active-hover-filter: $hamburger-hover-filter;\n\n$hamburger-types: (spin);\n\n@import '../../node_modules/hamburgers/_sass/hamburgers/hamburgers';\n\n.hamburger {\n  margin-top: 16px;\n  height: 38px;\n  padding: 0;\n  &:focus {\n    @include outline;\n  }\n  span.hamburger-box {\n    padding: 8px;\n    display: block;\n    outline: none;\n    box-shadow: none;\n  }\n}\n","@charset \"UTF-8\";\n/*!\n * Hamburgers\n * @description Tasty CSS-animated hamburgers\n * @author Jonathan Suh @jonsuh\n * @site https://jonsuh.com/hamburgers\n * @link https://github.com/jonsuh/hamburgers\n */\n\n// Settings\n// ==================================================\n$hamburger-padding-x           : 15px !default;\n$hamburger-padding-y           : 15px !default;\n$hamburger-layer-width         : 40px !default;\n$hamburger-layer-height        : 4px !default;\n$hamburger-layer-spacing       : 6px !default;\n$hamburger-layer-color         : #000 !default;\n$hamburger-layer-border-radius : 4px !default;\n$hamburger-hover-opacity       : 0.7 !default;\n$hamburger-active-layer-color  : $hamburger-layer-color !default;\n$hamburger-active-hover-opacity: $hamburger-hover-opacity !default;\n\n// To use CSS filters as the hover effect instead of opacity,\n// set $hamburger-hover-use-filter as true and\n// change the value of $hamburger-hover-filter accordingly.\n$hamburger-hover-use-filter   : false !default;\n$hamburger-hover-filter       : opacity(50%) !default;\n$hamburger-active-hover-filter: $hamburger-hover-filter !default;\n\n// Types (Remove or comment out what you dont need)\n// ==================================================\n$hamburger-types: (\n  3dx,\n  3dx-r,\n  3dy,\n  3dy-r,\n  3dxy,\n  3dxy-r,\n  arrow,\n  arrow-r,\n  arrowalt,\n  arrowalt-r,\n  arrowturn,\n  arrowturn-r,\n  boring,\n  collapse,\n  collapse-r,\n  elastic,\n  elastic-r,\n  emphatic,\n  emphatic-r,\n  minus,\n  slider,\n  slider-r,\n  spin,\n  spin-r,\n  spring,\n  spring-r,\n  stand,\n  stand-r,\n  squeeze,\n  vortex,\n  vortex-r\n) !default;\n\n// Base Hamburger (We need this)\n// ==================================================\n@import \"base\";\n\n// Hamburger types\n// ==================================================\n@import \"types/3dx\";\n@import \"types/3dx-r\";\n@import \"types/3dy\";\n@import \"types/3dy-r\";\n@import \"types/3dxy\";\n@import \"types/3dxy-r\";\n@import \"types/arrow\";\n@import \"types/arrow-r\";\n@import \"types/arrowalt\";\n@import \"types/arrowalt-r\";\n@import \"types/arrowturn\";\n@import \"types/arrowturn-r\";\n@import \"types/boring\";\n@import \"types/collapse\";\n@import \"types/collapse-r\";\n@import \"types/elastic\";\n@import \"types/elastic-r\";\n@import \"types/emphatic\";\n@import \"types/emphatic-r\";\n@import \"types/minus\";\n@import \"types/slider\";\n@import \"types/slider-r\";\n@import \"types/spin\";\n@import \"types/spin-r\";\n@import \"types/spring\";\n@import \"types/spring-r\";\n@import \"types/stand\";\n@import \"types/stand-r\";\n@import \"types/squeeze\";\n@import \"types/vortex\";\n@import \"types/vortex-r\";\n\n// ==================================================\n// Cooking up additional types:\n//\n// The Sass for each hamburger type should be nested\n// inside an @if directive to check whether or not\n// it exists in $hamburger-types so only the CSS for\n// included types are generated.\n//\n// e.g. hamburgers/types/_new-type.scss\n//\n// @if index($hamburger-types, new-type) {\n//   .hamburger--new-type {\n//     ...\n//   }\n// }\n","@use \"sass:math\";\n\n// Hamburger\n// ==================================================\n.hamburger {\n  padding: $hamburger-padding-y $hamburger-padding-x;\n  display: inline-block;\n  cursor: pointer;\n\n  transition-property: opacity, filter;\n  transition-duration: 0.15s;\n  transition-timing-function: linear;\n\n  // Normalize (<button>)\n  font: inherit;\n  color: inherit;\n  text-transform: none;\n  background-color: transparent;\n  border: 0;\n  margin: 0;\n  overflow: visible;\n\n  &:hover {\n    @if $hamburger-hover-use-filter == true {\n      filter: $hamburger-hover-filter;\n    }\n    @else {\n      opacity: $hamburger-hover-opacity;\n    }\n  }\n\n  &.is-active {\n    &:hover {\n      @if $hamburger-hover-use-filter == true {\n        filter: $hamburger-active-hover-filter;\n      }\n      @else {\n        opacity: $hamburger-active-hover-opacity;\n      }\n    }\n\n    .hamburger-inner,\n    .hamburger-inner::before,\n    .hamburger-inner::after {\n      background-color: $hamburger-active-layer-color;\n    }\n  }\n}\n\n.hamburger-box {\n  width: $hamburger-layer-width;\n  height: $hamburger-layer-height * 3 + $hamburger-layer-spacing * 2;\n  display: inline-block;\n  position: relative;\n}\n\n.hamburger-inner {\n  display: block;\n  top: 50%;\n  margin-top: math.div($hamburger-layer-height, -2);\n\n  &,\n  &::before,\n  &::after {\n    width: $hamburger-layer-width;\n    height: $hamburger-layer-height;\n    background-color: $hamburger-layer-color;\n    border-radius: $hamburger-layer-border-radius;\n    position: absolute;\n    transition-property: transform;\n    transition-duration: 0.15s;\n    transition-timing-function: ease;\n  }\n\n  &::before,\n  &::after {\n    content: \"\";\n    display: block;\n  }\n\n  &::before {\n    top: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;\n  }\n\n  &::after {\n    bottom: ($hamburger-layer-spacing + $hamburger-layer-height) * -1;\n  }\n}\n","// Theme Light\n\n$light-background: #fff;\n$light-background-seconday: #ecf2f2;\n$light-color: #000;\n$light-border: #dce8e8;\n$light-primary: #6db33f;\n$light-link: #086dc3;\n$light-placeholder: lighten($light-border, 5);\n$light-outline: darken($light-border, 15);\n\n// Theme Dark\n\n$dark-background: #1b1f23;\n$dark-background-secondary: #262a2d;\n$dark-color: #fff;\n$dark-border: #4a5053;\n$dark-primary: #6db33f;\n$dark-link: #086dc3;\n$dark-placeholder: #262a2d;\n\n// Global\n\n$spring-8points: 8px;\n$spring-font-size: 15px;\n$spring-font-size-sm: 14px;\n$spring-font-family: 'Metropolis', Arial, sans-serif;\n$spring-radius: 4px;\n$spring-transition-duration: 0.15s;\n$spring-max-width: 1680px;\n","@if index($hamburger-types, spin) {\n  /*\n   * Spin\n   */\n  .hamburger--spin {\n    .hamburger-inner {\n      transition-duration: 0.22s;\n      transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);\n\n      &::before {\n        transition: top 0.1s 0.25s ease-in,\n                    opacity 0.1s ease-in;\n      }\n\n      &::after {\n        transition: bottom 0.1s 0.25s ease-in,\n                    transform 0.22s cubic-bezier(0.55, 0.055, 0.675, 0.19);\n      }\n    }\n\n    &.is-active {\n      .hamburger-inner {\n        transform: rotate(225deg);\n        transition-delay: 0.12s;\n        transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);\n\n        &::before {\n          top: 0;\n          opacity: 0;\n          transition: top 0.1s ease-out,\n                      opacity 0.1s 0.12s ease-out;\n        }\n\n        &::after {\n          bottom: 0;\n          transform: rotate(-90deg);\n          transition: bottom 0.1s ease-out,\n                      transform 0.22s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);\n        }\n      }\n    }\n  }\n}\n","@mixin transition($args...) {\n  -webkit-transition: $args;\n  -moz-transition: $args;\n  -ms-transition: $args;\n  -o-transition: $args;\n  transition: $args;\n}\n\n@mixin clearfix {\n  &:after {\n    content: '';\n    display: table;\n    clear: both;\n  }\n}\n\n@mixin outline {\n  outline: 1px dotted transparent;\n  &:focus {\n    outline: 1px dotted $light-outline;\n    // outline: none;\n    // box-shadow: 0 0 0 2px $light-border;\n  }\n}\n","html {\n  font-size: $spring-font-size;\n  font-family: $spring-font-family;\n  color: $light-color;\n  font-weight: 300;\n  line-height: 1.8rem;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\nbody {\n  font-family: $spring-font-family;\n  background-color: $light-background;\n  margin: 0;\n}\n\n#app {\n  max-width: $spring-max-width;\n  margin: 0 auto;\n  position: relative;\n}\n\n#side-left,\n#side-right {\n  position: absolute;\n  .side-container {\n    width: 75px;\n    position: fixed;\n    top: 0;\n    z-index: 200;\n    min-height: 100%;\n    height: 100%;\n    text-align: center;\n  }\n}\n\n#side-left {\n  .side-container {\n    border-right: 1px solid rgba(darken($light-border, 15), 0.4);\n  }\n  a {\n    $size: 30px;\n    display: block;\n    margin: 0.2rem auto;\n    color: $light-color;\n    padding: 0;\n    cursor: pointer;\n    width: $size + 12px;\n    height: $size + 12px;\n    @include transition(color $spring-transition-duration);\n    @include outline;\n    .a-content {\n      display: block;\n      box-shadow: none;\n      outline: 0 none;\n      padding: 6px;\n      width: $size;\n      height: $size;\n    }\n    svg {\n      display: block;\n      width: $size;\n      height: $size;\n    }\n    &:hover {\n      color: $light-primary;\n    }\n  }\n  .social {\n    position: absolute;\n    bottom: 10px;\n    left: 0;\n    width: 5rem;\n    a {\n      $size: 30px;\n      svg {\n        width: $size;\n        height: $size;\n      }\n    }\n  }\n  .navigation-action {\n    position: absolute;\n    top: 20px;\n    left: 0;\n    width: 5rem;\n  }\n}\n\n#side-right {\n  right: 75px;\n  .side-container {\n    margin-left: auto;\n    background: $light-background;\n    padding: 20px 0;\n    z-index: 198;\n    border-left: 1px solid $light-border;\n  }\n}\n\n#main {\n  margin: 0 auto;\n  min-height: 100vh;\n  padding: 0 110px;\n  max-width: $spring-max-width;\n}\n\n// Header\n\n#header {\n  padding-top: 40px;\n  h1,\n  h2 {\n    margin: 0;\n  }\n  h1.logo {\n    max-width: 320px;\n    padding: 2rem 0;\n    a {\n      @include outline;\n      display: block;\n      margin-left: -6px;\n      color: $light-color;\n      .logo-content {\n        display: block;\n        outline: none;\n        box-shadow: none;\n        padding: 6px;\n      }\n      svg {\n        display: block;\n      }\n      .st0 {\n        fill: $light-primary;\n      }\n      .st1 {\n        fill: #ffffff;\n      }\n    }\n  }\n  h1.logo-mobile {\n    max-width: 140px;\n    padding: 1rem 12px;\n  }\n  .header-mobile {\n    border-bottom: 1px solid $light-border;\n    margin: 0 -12px 0.8rem -12px;\n    .hamburger {\n      position: absolute;\n      right: 10px;\n      top: 10px;\n    }\n  }\n  .switch-mobile {\n    position: absolute;\n    top: 28px;\n    right: 60px;\n    border-right: 1px solid $light-color;\n    height: 34px;\n    padding: 0 12px 0 8px;\n    button {\n      color: $light-color;\n      background-color: transparent;\n      border: 0;\n      padding: 4px;\n      outline: none;\n    }\n    svg {\n      display: block;\n      height: 26px;\n    }\n  }\n  h2 {\n    padding: 0.8rem 0 1rem;\n    strong,\n    span {\n      display: block;\n    }\n    strong {\n      font-size: 42px;\n      font-weight: 600;\n      line-height: 2.5rem;\n      padding-bottom: 0.4rem;\n    }\n    span {\n      font-weight: 400;\n      font-size: 22px;\n      line-height: 2rem;\n      font-weight: 600;\n    }\n  }\n  p {\n    font-size: 16px;\n    font-weight: 100;\n    line-height: 1.6rem;\n    margin-top: 0.3rem;\n  }\n}\n\n// Divider\n\nhr.divider {\n  border: 0 none;\n  border-top: 1px solid $light-border;\n  margin: 0.2rem 0 2rem;\n}\n\n// Colset\n\n.colset {\n  display: flex;\n  .left,\n  .right {\n    flex: 50%;\n  }\n  .left {\n    padding-right: 5rem;\n    flex: 50%;\n    .left {\n      padding-right: 1rem;\n    }\n  }\n  .right {\n  }\n}\n\n.col-sticky {\n  position: sticky;\n  top: 20px;\n}\n\n.colset-2 {\n  display: flex;\n  background: linear-gradient($light-border, $light-border) no-repeat 60%/1px\n    100%;\n  .column {\n    min-height: 240px;\n    flex: 40%;\n    &:first-child {\n      padding-right: 4rem;\n      flex: 60%;\n    }\n  }\n}\n\n// Control\n\n.control {\n  padding: 0.6rem 0;\n  .label {\n    font-weight: 600;\n    font-size: $spring-font-size + 2;\n  }\n}\n\n.control-inline {\n  display: flex;\n  label {\n    white-space: nowrap;\n    font-weight: 500;\n    flex: 110px 0;\n    text-align: right;\n  }\n  input,\n  .group-radio {\n    margin-left: 1.3rem;\n    font-weight: 300;\n    flex: 1;\n  }\n  input {\n    color: $light-color;\n    background: $light-background;\n    max-width: 520px;\n  }\n}\n\n.label-2 {\n  white-space: nowrap;\n  font-weight: 500;\n}\n\n// Radio\n\n.radio {\n  display: inline-block;\n  position: relative;\n  text-decoration: none;\n  color: $light-color;\n  margin-right: 1.5rem;\n  margin-left: -5px;\n  cursor: pointer;\n  line-height: 18px;\n\n  .radio-content {\n    display: inline-block;\n    padding: 6px;\n    padding-left: 30px;\n    outline: none;\n    box-shadow: none;\n  }\n  @include outline;\n  .caret {\n    display: block;\n    content: ' ';\n    width: 12px;\n    height: 12px;\n    background: $light-background;\n    position: absolute;\n    top: 6px;\n    left: 6px;\n    border: 2px solid $light-color;\n    box-shadow: none;\n    outline: none;\n    border-radius: 10px;\n  }\n  &:hover {\n    .caret {\n      border: 2px solid $light-primary;\n    }\n  }\n  &.checked {\n    color: $light-primary;\n    .caret {\n      background: $light-primary;\n      border-color: $light-primary;\n    }\n  }\n  &.err {\n    color: #d60000;\n    cursor: not-allowed;\n    .caret {\n      background: #d60000;\n      border: 2px solid #d60000;\n    }\n  }\n}\n\n// input\n\n.input {\n  display: block;\n  border: 0;\n  outline: none;\n  border-bottom: 3px solid $light-color;\n  line-height: 16px;\n  font-family: $spring-font-family;\n  font-size: 15px;\n  box-sizing: border-box;\n  background-clip: padding-box;\n  width: 100%;\n  padding: 0.35rem 0 0.55rem;\n  border-radius: 0;\n  background: #fff;\n  font-weight: 300;\n  &:focus {\n    border-color: $light-primary;\n  }\n  &::placeholder {\n    color: $light-color;\n    font-style: italic;\n    font-weight: 100;\n  }\n}\n\n// Panel\n\n.panel {\n  overflow: hidden;\n  max-height: 0;\n  @include transition(all $spring-transition-duration * 3);\n  &.panel-active {\n    max-height: 600px;\n  }\n  .panel-wrap {\n    border-top: 0 none;\n  }\n}\n\n// Button\n\na.button,\nbutton.button {\n  display: inline-block;\n  position: relative;\n  border-radius: 4px;\n  border: 2px solid $light-color;\n  color: $light-color;\n  padding: 0;\n  font-size: $spring-font-size - 1;\n  text-decoration: none;\n  text-transform: uppercase;\n  font-weight: 600;\n  line-height: 1.23rem;\n  overflow: hidden;\n  margin-right: 1rem;\n  cursor: pointer;\n  background: transparent;\n  @include transition(all calc($spring-transition-duration / 2));\n  z-index: 1;\n  background: $light-background;\n  font-family: $spring-font-family;\n  &:focus {\n    outline: none;\n    box-shadow: 0 0 0 4px $light-border;\n  }\n  span {\n    display: inline-block;\n    z-index: 1;\n    padding: 0.7rem 1.2rem 0.6rem;\n    &.secondary {\n      font-weight: 400;\n      margin-left: -10px;\n      padding-left: 0;\n    }\n    span span {\n      padding: 0;\n    }\n  }\n  span.button-content {\n    display: inline-block;\n    padding: 0;\n  }\n  &:before {\n    content: '';\n    display: block;\n    position: absolute;\n    background: $light-color;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    opacity: 0;\n    @include transition(all calc($spring-transition-duration / 2));\n    z-index: -1;\n  }\n  &:hover,\n  &:disabled {\n    color: white;\n    &:before {\n      opacity: 1;\n    }\n  }\n  &.active {\n    color: white;\n    &:before {\n      width: 100%;\n    }\n  }\n  &.inverse {\n    &:before {\n      bottom: 0;\n      width: 100%;\n      right: 0;\n      top: 0;\n      height: 100%;\n    }\n    &:hover {\n      &:before {\n        opacity: 1;\n      }\n    }\n    &.top {\n      &:before {\n      }\n    }\n    &.active {\n      color: white;\n      &:before {\n        opacity: 1;\n      }\n    }\n  }\n  &.lg {\n    font-size: $spring-font-size + 1;\n    span {\n      padding: 0.9rem 1.5rem 0.8rem;\n    }\n  }\n}\n\n// Buttons\n\n.buttons {\n  padding: 1rem 0;\n  .button:last-child {\n    margin-right: 0;\n  }\n}\n\n.buttons-center {\n  text-align: center;\n}\n\n// Switch\n\n.switch {\n  a.button {\n    $size: 20px;\n    padding: 0;\n    display: block;\n    height: auto;\n    width: 34px;\n    margin: 0 auto;\n    z-index: 1;\n    svg {\n      display: block;\n      width: $size;\n      height: $size;\n    }\n    &:first-child {\n      border-bottom: 0;\n      border-bottom-left-radius: 0;\n      border-bottom-right-radius: 0;\n    }\n    &:last-child {\n      border-top-left-radius: 0;\n      border-top-right-radius: 0;\n    }\n    &:focus {\n      z-index: 0;\n    }\n    .button-content {\n      display: inline-block;\n      outline: none;\n      box-shadow: none;\n      padding: 6px 7px 2px;\n    }\n  }\n}\n\n// Actions\n\n.actions {\n  display: block;\n  text-align: center;\n  bottom: 0;\n  z-index: 3;\n  position: fixed;\n  right: 75px;\n  left: 75px;\n  .actions-container {\n    background: $light-background-seconday;\n    padding: 20px 0.5rem;\n    margin: 0 auto;\n    max-width: $spring-max-width - (82 * 2);\n    height: 50px;\n  }\n  .button {\n    font-size: $spring-font-size + 1;\n    span {\n      padding: 0.9rem 1.5rem 0.8rem;\n    }\n    &:focus {\n      box-shadow: 0 0 0 4px darken($light-border, 6);\n    }\n    &:last-child {\n      margin-right: 0;\n    }\n  }\n}\n\n.colset-main {\n  padding-bottom: 90px;\n  background: linear-gradient($light-border, $light-border) no-repeat center/1px\n    100%;\n}\n\n.form {\n  position: relative;\n}\n\n.warnings {\n  background: $light-background;\n  margin-bottom: 1rem;\n  ul {\n    margin: 4px 0;\n  }\n}\n\n.control-error {\n  color: #d60000;\n  p {\n    margin: 0;\n    padding: 0;\n  }\n}\n\n.icon-time {\n  .st0 {\n    fill: none;\n    stroke: #000000;\n    stroke-width: 3;\n    stroke-miterlimit: 10;\n  }\n}\n\n.warnings {\n  $color: #e8ba00;\n  position: relative;\n  margin-bottom: $spring-8points;\n  padding: $spring-8points * 2;\n  line-height: 26px;\n  margin: 0 auto;\n  margin-bottom: 1rem;\n\n  border: 1px solid $light-border;\n  border-left: 6px solid $color;\n  color: darken($color, 8);\n  font-weight: 600;\n  .close {\n    display: block;\n    position: absolute;\n    top: 0;\n    right: 0;\n    padding: 12px 12px;\n    height: 28px;\n    cursor: pointer;\n    opacity: 0.7;\n    color: #8f2500;\n    svg {\n      width: 14px;\n    }\n    &:hover {\n      opacity: 1;\n    }\n  }\n  ul {\n    color: $light-color;\n    padding: 0;\n    padding-left: 20px;\n    margin: 0;\n    font-weight: 300;\n  }\n  strong.warn {\n    color: darken($color, 8);\n  }\n  li {\n    padding: 0;\n    margin: 0;\n  }\n}\n\n// Navigation\n\n.navigation {\n  background: $light-primary;\n  position: fixed;\n  z-index: 199;\n  left: 0;\n  width: 100%;\n  top: 0;\n  height: 100%;\n  color: white;\n  overflow-x: hidden;\n  overflow-y: auto;\n  .navigation-content {\n    padding: 0;\n    max-width: $spring-max-width;\n    margin: 0 auto;\n    .navigation-content-wrap {\n      margin: 0 110px;\n    }\n    #header h1.logo a .st0 {\n      fill: white;\n    }\n    #header h1.logo a .st1 {\n      fill: $light-primary;\n    }\n    #header h2 {\n      color: #222;\n    }\n    #header .switch-mobile {\n      display: none;\n    }\n  }\n  ul {\n    margin: 0;\n    padding: 0;\n    li {\n      list-style: none;\n      a {\n        font-size: 32px;\n        line-height: 60px;\n        font-weight: 600;\n        color: white;\n        text-decoration: none;\n        &:hover {\n          color: #222;\n        }\n        &:focus {\n          outline: none;\n          box-shadow: none;\n          color: #222;\n        }\n      }\n    }\n  }\n  .copyright {\n    padding-top: 2rem;\n    padding-bottom: 1rem;\n    a {\n      color: white;\n      &:hover {\n        color: #222;\n      }\n      &:focus {\n        box-shadow: none;\n        outline: none;\n        color: #222;\n      }\n    }\n  }\n}\n\n.navigation-enter {\n  top: -100%;\n}\n\n.navigation-enter-active {\n  transform: translateY(100%);\n  @include transition(transform 500ms);\n}\n\n.navigation-exit {\n  top: 0;\n}\n\n.navigation-exit-active {\n  transform: translateY(-100%);\n  @include transition(transform 500ms);\n}\n\n#side-left {\n  background: white;\n  @include transition(all 100ms);\n  &.is-open {\n    background: $light-primary;\n    border-right: 1px solid rgba(#fff, 0.2);\n    a {\n      color: white;\n      &:hover {\n        color: rgba(#fff, 0.7);\n      }\n    }\n  }\n}\n\n// Layout container\n\n.layout-container {\n  margin: 0 5rem;\n  height: 100%;\n  position: relative;\n  .layout-content {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 90px;\n    padding: 0 2rem;\n    overflow: auto;\n  }\n  .layout-actions {\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    right: 0;\n    background: $light-background-seconday;\n    padding: 20px 0.5rem;\n    z-index: 3;\n    height: 50px;\n    text-align: center;\n    margin: 0 -5rem;\n    .button {\n      font-size: $spring-font-size + 1;\n      span {\n        padding: 0.9rem 1.5rem 0.8rem;\n      }\n      &:focus {\n        box-shadow: 0 0 0 4px darken($light-border, 6);\n      }\n    }\n  }\n}\n\n// Explore dependencies\n\n.dialog-dependencies {\n  position: fixed;\n  z-index: 501;\n  $width: 800px;\n  width: $width;\n  margin-left: -(calc($width / 2));\n  left: 50%;\n  top: 40px;\n  bottom: 40px;\n  &-enter {\n    opacity: 0;\n  }\n  &-enter-active {\n    opacity: 1;\n    @include transition(all 300ms);\n  }\n  &-exit {\n    opacity: 1;\n  }\n  &-exit-active {\n    opacity: 0;\n    @include transition(all 300ms);\n  }\n  h1,\n  h2 {\n    margin: 0;\n    padding: 0;\n  }\n  h1 {\n    padding: 2rem 0 0.5rem;\n    font-size: 30px;\n  }\n  h2 {\n    padding: 1.5rem 0 0.5rem;\n    font-size: 24px;\n  }\n  ul {\n    padding: 0;\n    margin: 0;\n    position: absolute;\n    top: 50px;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    overflow: auto;\n    &::-webkit-scrollbar {\n      height: 6px;\n      width: 6px;\n      background: $light-background;\n    }\n    &::-webkit-scrollbar-thumb {\n      background: rgba(black, 0.3);\n    }\n    li {\n      background: $light-background;\n      padding: 0;\n      margin: 0;\n      list-style: none;\n      border-top: 1px solid $light-border;\n      a.dependency {\n        display: block;\n        position: relative;\n        line-height: 1.7rem;\n        color: $light-color;\n        text-decoration: none;\n        padding: 12px 1rem;\n        padding-right: 45px;\n        span,\n        strong {\n          display: block;\n          cursor: pointer;\n        }\n        span.group {\n          display: inline-block;\n          background: $light-primary;\n          color: #fff;\n          font-size: $spring-font-size - 2;\n          padding: 4px 0.6rem 1px;\n          line-height: 18px;\n          margin-left: 0.6rem;\n          text-transform: uppercase;\n        }\n        strong {\n          font-size: $spring-font-size + 2;\n          font-weight: 600;\n        }\n        .icon-enter {\n          display: none;\n          $height: 18px;\n          fill: #fff;\n          position: absolute;\n          top: 50%;\n          margin-top: -(calc($height / 2));\n          height: $height;\n          right: 15px;\n        }\n        &.selected {\n          color: white;\n          background: $light-primary;\n          margin-bottom: -1px;\n          margin-top: -1px;\n          padding: 13px 1rem;\n          padding-right: 45px;\n          box-shadow: inset 0 1px 0 rgba(white, 0.2);\n          &:hover {\n            strong {\n              color: white;\n            }\n          }\n          span.group {\n            background: white;\n            color: $light-color;\n          }\n          .icon-enter {\n            display: block;\n          }\n        }\n        &.disabled,\n        &.disabled:hover,\n        &.disabled.selected {\n          background: white;\n          cursor: not-allowed;\n    